import React, {useState, useEffect, setState} from 'react';
import axios from 'axios';

class ReportData extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            //EXPECTS SESSION STORAGE
            userId: sessionStorage.getItem("userId"), 
            companyId: this.props.companyId,
            accepted: 0,
            gpa: sessionStorage.getItem("gpa"),
            college: sessionStorage.getItem("college"),
            collegeYear: sessionStorage.getItem("collegeYear"),
            previousInternships: sessionStorage.getItem("previousInternships")
        };
    }
    
    refreshPage = () => {
        window.location.reload(false);
    }

    clickedAccepted = () => {
        this.state.accepted = 1;
        console.log(this.state)
        axios
            .post("http://34.68.104.62:8090/companies/add_report", this.state)
            .then(response => {
                console.log(response)
                this.refreshPage()
            }).catch(error => {
                console.log(error)
            })
        return
    }

    clickedRejected = () => {
        this.state.accepted = 0;
        console.log(this.state)
        axios
            .post("http://34.68.104.62:8090/companies/add_report", this.state)
            .then(response => {
                console.log(response)
                this.refreshPage()
            }).catch(error => {
                console.log(error)
            })
        return
    }
    
    render() {
        return (
        <div className="btns">
            <button className="btn-box" onClick={this.clickedAccepted}>Accepted</button>
            <button className="btn-box" onClick={this.clickedRejected}>Rejected</button>
        </div>
        );
    }
}

export default ReportData;