import React, { Component } from 'react'
import axios from 'axios'

class ResForm extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            userId: sessionStorage.getItem("userId"), //EXPECTS SESSION STORAGE
            fileUrl: '',
            title: '',
            description: '',
            username: sessionStorage.getItem("username"), //EXPECTS SESSION STORAGE
            selectedFile: ''
        }
    }
    
    changeHandler = (e) => {
        this.setState({[e.target.name]: e.target.value})
    }

    fileHandler = (e) => {
        this.setState({selectedFile: e.target.files[0]})
    }

    refreshPage = () => {
        window.location.reload(false);  
    }

    onFileUpload = () => {
    
        // Create an object of formData
        const formData = new FormData();
      
        // Update the formData object
        formData.append(
          "image",
          this.state.selectedFile,
          this.state.selectedFile.name
        );
      
        // Details of the uploaded file
        console.log(this.state.selectedFile);
      
        // Request made to the backend api
        // Send formData object
        axios.post('https://cscareers-proj.uc.r.appspot.com/upload', formData)
            .then(response => {
                this.setState({ fileUrl: response.data})
                console.log(response)
            })
            //.then(response => )
            
            .catch(error => {
                console.log(error)
            })
      };
    
    submitHandler = (e) => {
        e.preventDefault()
        const resumeUp = {
            username: this.state.username,
            description: this.state.description,
            title: this.state.title,
            fileUrl: this.state.fileUrl
        }
        console.log(resumeUp);
        axios
            .post("http://34.68.104.62:8090/resumes/post/resume", resumeUp)
            .then(response => {
                console.log(response)
                this.refreshPage()
            })
            .catch(error => {
                console.log(error)
            })
    }

    render() {
        const { userId, fileUrl, title, description, username } = this.state
        return (
            <div className ="create-resume-form-holder">
                <h2 className="resume-need-help-header">Need help with your resume?</h2>
                <div className="stepOne">
                <input type="file" name="image" onChange={this.fileHandler} className="submit-file-input"/><br/>
                <button className="upload-file-button" onClick={this.onFileUpload} className="submit-file-button">Upload file</button>
                </div>
                <form onSubmit={this.submitHandler} className="submit-resume-form">
                    <input type="text" name="title" placeholder="title" value={title} onChange={this.changeHandler} className="submit-resume-form-field"/><br/> 
                    <input type="text" name="description" placeholder="description" value={description} onChange={this.changeHandler} className="submit-resume-form-field"/><br/>
                    <input type="submit" value="enter info" className="submit-resume-form-submit"/> 
                </form>
            </div>
        )
    }
}

export default ResForm
