import React, {useState, useEffect, setState} from 'react';
import axios from 'axios';
import './../Projects.css';

class ProjectForm extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            userId: sessionStorage.getItem("userId"), //EXPECTS SESSION STORAGE
            projectTitle: "",
            projectDescription: "",
            projectTags: "",
            lookingForTags: "",
            joinUrl: ""
        };
    }
    
    refreshPage = () => {
        window.location.reload(false);
    }
    
    myChangeHandler = (event) => {
        this.setState({[event.target.name]: event.target.value})
    }
    
    mySubmitHandler = (event) => {
        event.preventDefault()
        console.log(this.state)
        axios
            .post("http://34.68.104.62:8090/projects/post", this.state)
            .then(response => {
                console.log(response)
                this.refreshPage()
            }).catch(error => {
                console.log(error)
            })
    }
    
    render() {
        const {projectTitle, projectDescription, projectTags, lookingForTags, joinUrl} = this.state
        return (
            <main id="create-project-holder">
                <h2 id="create-project-header">Create a project</h2>
                <form id="create-project-form" onSubmit={this.mySubmitHandler}>
                    <input type="text" value={projectTitle} className="create-project-form-field" name="projectTitle" placeholder="Project Title" onChange={this.myChangeHandler}></input>
                    <input type="textarea" value={projectDescription} className="create-project-form-field" name="projectDescription" placeholder="Project Description" onChange={this.myChangeHandler}></input>
                    <input type="text" value={projectTags} className="create-project-form-field" name="projectTags" placeholder="Project Tags" onChange={this.myChangeHandler}></input>
                    <input type="text" value={lookingForTags} className="create-project-form-field" name="lookingForTags" placeholder="Looking For Tags" onChange={this.myChangeHandler}></input>
                    <input type="text" value={joinUrl} className="create-project-form-field" name="joinUrl" placeholder="Join URL" onChange={this.myChangeHandler}></input>
                    <input type="submit" className="create-project-form-submit" value="Submit"></input>
                </form>
            </main>
        );
    }
}

export default ProjectForm;